import opg from 'ts/opg.ts';
import {api} from 'ts/util/api';
import {request} from "/ts/util/utils";


api({
	getMain: 'mediaexhibtion/GetMediaExhibitonInfoSubjectById',
	getSub: 'mediaexhibtion/MediaExhibitionInfoById',
});

const informationId = request['id'];


$.when(
	api.getMain({id: informationId} , data=>{
		document.getElementById('main').innerHTML = template('tpl-main', data[0]);
	}),
).then(()=>{
	api.getSub({informationId} , data=>{
		if(data.length){
			let html = '';
			data.forEach(v=>{
				html += `<p>${v.content}</p>`
			});
			document.getElementById('sub').innerHTML = html;
		}
	})
});

